/*** The new CSS Reset - version 1.2.0 (last updated 23.7.2021) ***/
/* Remove all the styles of the "User-Agent-Stylesheet", except for the 'display' property */
*:where(:not(iframe, canvas, img, svg, video):not(svg *)) {
  all: unset;
  display: revert;
}

/* Preferred box-sizing value */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove list styles (bullets/numbers) */
ol, ul {
  list-style: none;
}

/* removes spacing between cells in tables */
table {
  border-collapse: collapse;
}

/* revert the 'white-space' property for textarea elements on Safari */
textarea {
  white-space: revert;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--default-font-style);
  font-size: var(--default-font-size);
  overflow-x: hidden;
}

:root {
  --default-font-style: "Poppins", sans-serif;
  --default-font-size: 18px;
}

:root {
  --cyan: hsl(180, 66%, 49%);
  --white: hsl(0, 0%, 100%);
  --dark-violet: hsl(257, 27%, 26%);
  --red: hsl(0, 87%, 67%);
  --gray: hsl(0, 0%, 75%);
  --grayish-violet: rgb(158, 154, 167);
  --very-dark-blue: hsl(255, 11%, 22%);
  --vary-dark-violet: hsl(260, 8%, 14%);
}

.header__wrapper {
  background-color: var(--white);
  position: relative;
}
.header__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 0;
}
.header__item-image > a {
  display: flex;
}
.header__item-button {
  cursor: pointer;
}
@media (min-width: 48em) {
  .header__item-button {
    display: none;
  }
}
.header__item-button .bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  background-color: var(--grayish-violet);
}
.header__item-button.is_active > .bar:nth-child(2) {
  opacity: 0;
}
.header__item-button.is_active > .bar:nth-child(1) {
  transform: translateY(8px) rotate(45deg);
}
.header__item-button.is_active > .bar:nth-child(3) {
  transform: translateY(-8px) rotate(-45deg);
}
.header__nav {
  display: flex;
  justify-content: center;
  text-align: center;
  background-color: var(--dark-violet);
  border-radius: 10px;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.header__nav-menu {
  padding: 2rem 0;
  width: 80%;
}
.header__nav-list:not(:last-child) {
  line-height: 3.5;
}
.header__nav-list:nth-child(3) {
  padding-bottom: 1rem;
}
.header__nav-list:nth-child(4) {
  border-top: 1px solid rgba(158, 154, 167, 0.2);
  padding-top: 1rem;
}
.header__nav-list:nth-child(5) {
  background-color: var(--cyan);
  border-radius: 50px;
  padding: 0.5rem 0;
}
.header__nav-list > .header__nav-anchor {
  font-size: var(--default-font-size);
  font-weight: 700;
  color: var(--white);
  cursor: pointer;
}
.header__nav.is_active {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}

.container {
  width: min(90rem, 100%);
  margin-left: auto;
  margin-right: auto;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
}
@media (min-width: 48em) {
  .container {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
  }
}